.item {
  display: flex;
  width: fit-content;
  align-items: center;
  box-shadow: 0px 10px 5px 0px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  padding: 10px;
  gap: 10px;
}

.status {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: block;
}

.online {
  background-color: green;
}

.offline {
  background-color: red;
}

.avatar {
  width: 48px;
}

.name {
  min-width: 100px;
}